import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import useLocales from '../../hooks/useLocales';
import { Icon } from '@iconify/react';

import { API_CATEGORY, API_EVENT, API_LOCATION } from '../../config';
import axios from '../../utils/axios';
import dayjs from 'dayjs';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import { SearchDropdown, SearchDateDropdown } from '../../sections/events/SearchDropdown';

export default function SearchEvents() {
    const { t } = useLocales();

    const navigate = useNavigate();
    const [events, setEvents] = useState([]);
    const [locations, setLocations] = useState([]);
    const [categories, setCategories] = useState([]);

    const [categoryValue, setCategoryValue] = useState("all");
    const [dateValue, setDateValue] = useState("all");
    const [locationValue, setLocationValue] = useState("all");
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);

    const formatDate = (date) => {
        return new Date(date).toDateString();
    }

    const onChangeEvent = (eventId) => {
        navigate(`/event/${eventId}/basic`);
    };

    const handleChange = (e, type) => {
        if (type === 'location')
            setLocationValue(e);
        if (type === 'category')
            setCategoryValue(e);
        if (type === 'date') {
            setDateValue(e);
        }
    }

    const getList = () => {
        setLoading(true);
        axios
            .get(`${API_EVENT.getList}`)
            .then(({ data }) => {
                setEvents(data.data);
                setLoading(false);
            })
            .catch((res) => {
                setLoading(false);
            });
    }

    const getLocation = () => {
        setLoading(true);
        axios
            .get(`${API_LOCATION.root}`)
            .then(({ data }) => {
                setLocations(data.data);
                setLoading(false);
            })
            .catch((res) => {
                setLoading(false);
            });
    }

    const getCategory = () => {
        setLoading(true);
        axios
            .get(`${API_CATEGORY.root}`)
            .then(({ data }) => {
                setCategories(data.data);
                setLoading(false);
            })
            .catch((res) => {
                setLoading(false);
            });
    }

    useEffect(() => {
        const data = {
            category: categoryValue,
            location: locationValue,
            date: dateValue
        }
        axios
            .post(`${API_EVENT.getFilterList}`, data)
            .then(({ data }) => {
                setEvents(data.data);
                setLoading(false);
            })
            .catch((res) => {
                setLoading(false);
            });
    }, [categoryValue, locationValue, dateValue])

    useEffect(() => {
        getList();
        getLocation();
        getCategory();
    }, []);

    return (
        <Page title="Search Events">
            <PageBanner>
                <div className="container mb-8 px-2">
                    <div className="flex flex-col justify-center w-full gap-2 mb-16">
                        <h4 className="text-4xl md:text-[64px]">{t('title.search_events')}</h4>
                        <p className="text-stone-400">
                            {t('description.search_events')}
                        </p>
                    </div>
                    <div className="flex flex-col justify-center w-full gap-8">
                        <div className='flex gap-6 flex-wrap' >
                            <SearchDropdown
                                label={t("title.ticket_ads_location")}
                                value={locationValue}
                                options={locations}
                                icon={"heroicons:map-pin-solid"}
                                handleChange={(id) => handleChange(id, "location")}
                            />
                            <SearchDateDropdown
                                label={t("title.date")}
                                value={dateValue}
                                icon={"gala:calendar"}
                                handleChange={(e) => handleChange(e, "date")}
                            />
                            <SearchDropdown
                                label={t("title.category")}
                                value={categoryValue}
                                options={categories}
                                icon={"bx:category"}
                                handleChange={(id) => handleChange(id, "category")}
                            />
                        </div>
                        {events.length ? (
                            <div className="grid md:grid-cols-3 gap-8">
                                {
                                    events.slice(0, 18 * step).map((event, i) => (
                                        <div key={i} onClick={() => onChangeEvent(event?._id)} className='bg-[#2C2C2C] rounded-2xl cursor-pointer hover:bg-[#2C2C2C]/50'>
                                            <div className="flex gap-2 justify-between py-4 px-6 items-center">
                                                <div className="flex flex-col ">
                                                    <h5 className="text-[19px]">{event.name}</h5>
                                                    <p className="text-stone-400 text-[11px]">{`${formatDate(event.dateTime)} • ${event.location.city.name} • ${event.location.city.countryCode}`}</p>
                                                </div>
                                                <div>
                                                    <div
                                                        className={`bg-base-300 rounded-full px-3 py-1 flex justify-center items-center text-primary`}
                                                    >
                                                        <Icon icon="system-uicons:ticket" width={20} />
                                                        <h6 className="text-base">3</h6>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        ) : (
                            <div className="flex flex-col w-full gap-2">
                                <h4 className="text-5xl">{t('message.no_events_fount')}</h4>
                                <p className="text-stone-400">
                                    {t('message.filters_no_result')}
                                </p>
                            </div>
                        )}

                        {events?.length > 18 && (<div className='flex justify-center items-center gap-4 w-full' >
                            <button
                                className="btn border-primary btn-circle btn-sm w-10 h-10"
                                onClick={() => {
                                    setStep(step++);
                                }}
                            >
                                <Icon className="text-primary text-3xl" icon="ic:outline-plus" />
                            </button>
                            <h5 className="text-[19px]">{t('action.more_events')}</h5>
                        </div>)}
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
