import { useState } from 'react';
import { Icon } from '@iconify/react';
import { Link } from 'react-router-dom';
import useLocales from '../../hooks/useLocales';

import { Dialog, DialogTitle, DialogContent } from '@mui/material';

import dayjs from 'dayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DemoContainer, DemoItem } from '@mui/x-date-pickers/internals/demo';
import { AdapterDayjs } from '@mui/x-date-pickers-pro/AdapterDayjs';
import { DateRangePicker } from '@mui/x-date-pickers-pro/DateRangePicker';
import { StaticDateRangePicker } from '@mui/x-date-pickers-pro/StaticDateRangePicker';
import { pickersLayoutClasses } from '@mui/x-date-pickers/PickersLayout';
import { LicenseInfo } from '@mui/x-license-pro';
LicenseInfo.setLicenseKey('x0jTPl0USVkVZV0SsMjM1kDNyADM5cjM2ETPZJVSQhVRsIDN0YTM6IVREJ1T0b9586ef25c9853decfa7709eee27a1e');

export function SearchDropdown({ label, icon, value, options, handleChange }) {
    const { t } = useLocales();
    const active = options.filter((item) => item._id === value);

    const handleActiveItem = (id) => {
        handleChange(id);
        handleClose();
    }

    const handleClose = () => {
        const ele = document.activeElement;
        ele?.blur();
    };
    return (
        <div className="dropdown">
            <div className="flex gap-4 px-6 py-[14px] items-center cursor-pointer bg-[#151515] rounded-2xl" tabIndex={0}>
                <div
                    className="p-[12px] capitalize border-2 border-primary rounded-[14px] h-auto min-h-0"
                >
                    <Icon icon={icon} className='text-primary w-[27px] h-[27px]' />
                </div>
                <div className='flex flex-col'>
                    <h5 className="text-2xl">{label}</h5>
                    <p className="text-stone-400 text-[14px]">
                        {active.length ? active[0].name ? active[0].name : active[0].city.name : "all"}
                    </p>
                </div>
                <Icon icon="ic:outline-keyboard-arrow-down" className='text-2xl' />
            </div>
            <ul className="dropdown-content mt-2 menu p-2 shadow bg-base-200 rounded-b-box w-72">
                {options.map((option, i) => (
                    <li key={i}>
                        <Link onClick={() => handleActiveItem(option._id)} className={`text-[17px] py-2 ${active.length ? option._id === active[0]._id ? 'active' : '' : ''}`} >
                            {option.name ? option.name : `${option.city.name} • ${option.city.countryCode}`}
                        </Link>
                    </li>
                ))}
            </ul>
        </div>
    );
}

export function SearchDateDropdown({ label, icon, value, handleChange }) {
    const { t } = useLocales();

    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleAccept = (val) => {
        handleChange(val);
        handleClose();
    }

    const formatDate = (date) => {
        return new Date(date).toDateString();
    }

    return (
        <div className="dropdown">
            <div className="flex gap-4 px-6 py-[14px] items-center cursor-pointer bg-[#151515] rounded-2xl" onClick={handleClickOpen} tabIndex={0}>
                <div
                    className="p-[12px] capitalize border-2 border-primary rounded-[14px] h-auto min-h-0"
                >
                    <Icon icon={icon} className='text-primary w-[27px] h-[27px]' />
                </div>
                <div className='flex flex-col'>
                    <h5 className="text-2xl">{label}</h5>
                    <p className="text-stone-400 text-[14px]">
                        {value !== 'all' ? `${formatDate(value[0])} ~ ${formatDate(value[1])}` : value}
                    </p>
                </div>
                <Icon icon="ic:outline-keyboard-arrow-down" className='text-2xl' />
            </div>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogContent>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <StaticDateRangePicker
                            value={value !== 'all' ? value : [dayjs(), dayjs()]}
                            sx={{
                                [`.${pickersLayoutClasses.contentWrapper}`]: {
                                    alignItems: 'center',
                                },
                            }}
                            onClose={handleClose}
                            onAccept={handleAccept}
                        />
                    </LocalizationProvider>
                </DialogContent>
            </Dialog>
        </div>
    );
}